import React from 'react';
import s from './Post.module.css';
import {PropsType} from './AddPostForm';

const Post: React.FC<PropsType> = (props) => {
    return (
        <div className={s.item}>
            <img src='https://weblinks.ru/wp-content/uploads/2021/04/3zeynnx6ija.jpg' alt='' />
            { props.message }
            <div>
                <span className={s.likesLogo}><img src='https://www.pngfind.com/pngs/m/6-62693_facebook-heart-transparent-facebook-heart-icon-hd-png.png' alt='like'/>
                    { props.likesCount } likes</span>

            </div>
        </div>
    )
}

export default Post