import React, {lazy, Suspense} from 'react'
import './App.css'
import Navbar from './components/Navbar/Navbar'
import {BrowserRouter, Navigate, Route, Routes} from 'react-router-dom'
import {UsersPage} from './components/Users/UsersContainer'
import ProfileContainer, {withRouter} from './components/Profile/ProfileContainer'
import SidebarContainer from './components/Navbar/Sidebar/SidebarContainer'
import HeaderContainer from './components/Header/HeaderContainer'
import {LoginPage} from './components/Login/LoginPage'
import {connect, Provider} from 'react-redux'
import {compose} from 'redux'
import {initializeApp} from './redux/app-reducer'
import Preloader from './components/common/preloader/Preloader'
import {withSuspense} from './components/hoc/withSuspense'
import store, {AppStateType} from './redux/redux-store'


const DialogsContainer = withSuspense(lazy(() => import('./components/Dialogs/DialogsContainer')))

type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
    initializeApp: () => void
}

class App extends React.Component<MapPropsType & DispatchPropsType> {
    catchAllUnhandledErrors = (e: PromiseRejectionEvent) => {
        alert('Some error occured')
    }

    componentDidMount() {
        this.props.initializeApp()
        window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }

    componentWillUnmount() {
        window.removeEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className="app-wrapper">
                <HeaderContainer/>
                <Navbar/>
                <div className="app-wrapper-content">
                    <Suspense fallback={<div><Preloader/></div>}>
                        <Routes>
                            <Route path="/" element={<Navigate to="/profile"/>}/>
                            <Route path="/profile/:userId?" element={<ProfileContainer/>}/>
                            <Route path="/dialogs/*" element={<DialogsContainer/>}/>
                            <Route path="/users/*" element={<UsersPage pageTitle={'Users'}/>}/>
                            <Route path="/login" element={<LoginPage/>}/>
                            <Route path="/sidebar" element={<SidebarContainer/>}/>
                        </Routes>
                    </Suspense>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state: AppStateType) => ({
    initialized: state.app.initialized
})

const AppContainer = compose<React.ComponentType>(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App)

const InfinityApp: React.FC = () => {
    return <BrowserRouter>

        <Provider store={store}>
            <React.StrictMode>
                <AppContainer />
            </React.StrictMode>
        </Provider>
    </BrowserRouter>
}

export default InfinityApp

